import {
  CButton,
  CCol,
  CContainer,
  CFormInput,
  CFormLabel,
  CImage,
  CRow,
} from "@coreui/react";
import axios from "axios";
import { useCallback, useEffect, useState } from "react";
import { api } from "../../api";

const Profile = () => {
  const [data, setData] = useState({
    firstName: "",
    lastName: "",
    countryCode: "",
    phoneNumber: "",
    email: "",
    storeName: "",
    storeDescription: "",
    storeAddress: "",
  });
  const [file, setFile] = useState("");
  const [fileData, setFileData] = useState("");

  const handleChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handleUpload = useCallback(
    (id) => {
      if (fileData && id) {
        try {
          const bodyFormData = new FormData();
          bodyFormData.append("avatar", fileData);

          axios({
            method: "post",
            url: `${api}/api/user/upload/${id}`,
            data: bodyFormData,
            headers: {
              "Content-Type": "multipart/form-data",
              "x-auth-token": localStorage.token,
            },
          }).then(() => {
            window.location.reload();
          });
        } catch (error) {
          console.log(error?.response);
        }
      } else {
        window.location.reload();
      }
    },
    [fileData]
  );

  const onSubmit = async (e) => {
    e.preventDefault();
    const updateData = {
      firstName: data?.firstName,
      lastName: data?.lastName,
      countryCode: data?.countryCode,
      phoneNumber: data?.phoneNumber,
      email: data?.email,
      store: {
        name: data?.storeName,
        description: data?.storeDescription,
        address: data?.storeAddress,
      },
    };
    await axios.put(`${api}/api/user/${localStorage.userId}`, updateData, {
      headers: {
        "Content-Type": "application/json",
        "x-auth-token": localStorage.token,
      },
    });

    handleUpload(localStorage.userId);
  };

  useEffect(async () => {
    try {
      const response = await axios.get(`${api}/api/user/me`, {
        headers: {
          "x-auth-token": localStorage.token,
        },
      });

      setData({
        firstName: response?.data?.firstName,
        lastName: response?.data?.lastName,
        countryCode: response?.data?.countryCode,
        phoneNumber: response?.data?.phoneNumber,
        email: response?.data?.email,
        storeName: response?.data?.store?.name,
        storeDescription: response?.data?.store?.description,
        storeAddress: response?.data?.store?.address,
      });

      if (response?.data?.avatar) {
        setFile(
          `${api}/uploads/customer/${localStorage.userId}/${response?.data?.avatar}`
        );
      }
    } catch (error) {
      console.log(error);
    }
  }, []);

  return (
    <CContainer>
      <CRow>
        <CCol sm={12}>
          <div className="mb-3">
            {file && !file?.includes("undefined") ? (
              <div
                style={{
                  borderRadius: "50%",
                  overflow: "hidden",
                  width: 80,
                  height: 80,
                }}
              >
                <CImage
                  fluid
                  src={file}
                  className="mb-3"
                  style={{ objectFit: "cover", width: "100%", height: "100%" }}
                />
              </div>
            ) : null}
            <div className="form-file">
              <input
                type="file"
                className="form-file-input"
                id="customFile"
                style={{ display: "none" }}
                onChange={(e) => {
                  setFileData(e.target.files[0]);
                  setFile(URL.createObjectURL(e.target.files[0]));
                }}
              />
              <label className="form-file-label" htmlFor="customFile">
                <span className=" m-3">ویرایش عکس پروفایل</span>
                <span className="btn btn-primary">کلیک کنید</span>
              </label>
            </div>
          </div>
        </CCol>
        <CCol sm={12} md={6}>
          <div className="mb-3">
            <CFormLabel htmlFor="firstName">نام</CFormLabel>
            <CFormInput
              id="firstName"
              name="firstName"
              value={data?.firstName}
              onChange={handleChange}
              placeholder="نام خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12} md={6}>
          <div className="mb-3">
            <CFormLabel htmlFor="lastName">نام خانوادگی</CFormLabel>
            <CFormInput
              id="lastName"
              name="lastName"
              value={data?.lastName}
              onChange={handleChange}
              placeholder="نام خانوادگی خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12} md={1}>
          <div className="mb-3">
            <CFormLabel htmlFor="countryCode">کد کشور</CFormLabel>
            <CFormInput
              id="countryCode"
              name="countryCode"
              value={data?.countryCode}
              onChange={handleChange}
              placeholder="کد کشور خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12} md={5}>
          <div className="mb-3">
            <CFormLabel htmlFor="phoneNumber">شماره همراه</CFormLabel>
            <CFormInput
              id="phoneNumber"
              name="phoneNumber"
              value={data?.phoneNumber}
              onChange={handleChange}
              placeholder="شماره همراه خود را بدون صفر اول وارد کنید: ۹۱۲۳۴۵۶۷۸۹"
            />
          </div>
        </CCol>
        <CCol sm={12} md={6}>
          <div className="mb-3">
            <CFormLabel htmlFor="email">ایمیل</CFormLabel>
            <CFormInput
              disabled
              id="email"
              name="email"
              value={data?.email}
              onChange={handleChange}
              placeholder="ایمیل خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12} md={6}>
          <div className="mb-3">
            <CFormLabel htmlFor="storeName">نام فروشگاه</CFormLabel>
            <CFormInput
              id="storeName"
              name="storeName"
              value={data?.storeName}
              onChange={handleChange}
              placeholder="نام فروشگاه خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12} md={6}>
          <div className="mb-3">
            <CFormLabel htmlFor="storeDescription">
              توضیحات مختصر فروشگاه
            </CFormLabel>
            <CFormInput
              id="storeDescription"
              name="storeDescription"
              value={data?.storeDescription}
              onChange={handleChange}
              placeholder="توضیحات مختصر فروشگاه خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12} md={12}>
          <div className="mb-3">
            <CFormLabel htmlFor="storeAddress">آدرس فروشگاه</CFormLabel>
            <CFormInput
              id="storeAddress"
              name="storeAddress"
              value={data?.storeAddress}
              onChange={handleChange}
              placeholder="آدرس فروشگاه خود را وارد کنید"
            />
          </div>
        </CCol>
        <CCol sm={12}>
          <div className="mb-3">
            <CButton onClick={onSubmit}>ویرایش اطلاعات</CButton>
          </div>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default Profile;
