import { cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormTextarea,
  CRow,
} from '@coreui/react';
import axios from 'axios';
import { useCallback, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import 'slick-carousel/slick/slick-theme.css';
import 'slick-carousel/slick/slick.css';
import { api, headers } from 'src/views/api';

const AddProduct = () => {
  const [product, setProduct] = useState({
    name: '',
    description: '',
    price: '',
    categories: '',
  });
  const [categories, setCategoeies] = useState([]);
  const [updateMode, setUpdateMode] = useState(false);
  const [file, setFile] = useState('');
  const [fileData, setFileData] = useState([]);
  const [images, setImages] = useState([]);
  const [filters, setFilters] = useState([]);
  const [filter, setFilter] = useState([]);
  const [selectedFilterChild, setSelectedFilterChild] = useState([]);
  const [subFilters, setSubFilters] = useState([]);
  const [subFilter, setSubFilter] = useState([]);
  const [parent, setParent] = useState(null);
  const { push } = useHistory();

  function isNumeric(num) {
    return !isNaN(num);
  }

  const addCommas = (num) =>
    num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  const removeNonNumeric = (num) => num.toString().replace(/[^0-9]/g, '');

  const handleChange = (e) => {
    if (e.target.name === 'price') {
      const p = Number(removeNonNumeric(e.target.value));
      if (isNumeric(p)) {
        setProduct({ ...product, price: addCommas(p) });
      } else {
        return;
      }
    } else {
      setProduct({ ...product, [e.target.name]: e.target.value });
    }
  };

  const handleUpload = useCallback(
    async (id) => {
      if (fileData?.length && id) {
        try {
          for (let i = 0; i < fileData.length; i++) {
            const bodyFormData = new FormData();
            bodyFormData.append('image', fileData[i]);

            await axios({
              method: 'post',
              url: `${api}/api/product/upload/${id}`,
              data: bodyFormData,
              headers: {
                'Content-Type': 'multipart/form-data',
                'x-auth-token': localStorage.token,
              },
            });
          }
        } catch (error) {
          console.log(error?.response);
        }
      }
    },
    [fileData]
  );

  const persianNumbers = [
      /۰/g,
      /۱/g,
      /۲/g,
      /۳/g,
      /۴/g,
      /۵/g,
      /۶/g,
      /۷/g,
      /۸/g,
      /۹/g,
    ],
    arabicNumbers = [
      /٠/g,
      /١/g,
      /٢/g,
      /٣/g,
      /٤/g,
      /٥/g,
      /٦/g,
      /٧/g,
      /٨/g,
      /٩/g,
    ],
    fixNumbers = function (str) {
      if (typeof str === 'string') {
        for (var i = 0; i < 10; i++) {
          str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
        }
      }
      return str;
    };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      product.price = removeNonNumeric(fixNumbers(product.price));
      product.discount = fixNumbers(product.discount);
      product.filters = filter;
      product.subFilter = subFilter;

      const response = await axios.post(`${api}/api/product`, product, {
        headers,
      });

      if (response?.status === 200) {
        handleUpload(response?.data?._id);
        toast.success('محصول با موفقیت ثبت شد');
      }
    } catch (error) {
      toast.error(
        'نام محصول ,دسته بندی و قیمت الزامی می باشد. لطفا آنها را تکمیل کنید و سپس فرم را ثبت کنید.'
      );
    }
  };

  const renderCategories = (categories) => {
    let myCategories = [];

    for (let category of categories) {
      myCategories.push(
        <ul style={{ padding: 8 }}>
          <li style={{ listStyleType: 'none' }}>
            <input
              style={{
                marginLeft: 8,
                position: 'relative',
                top: 2,
                cursor: 'pointer',
              }}
              type='checkbox'
              value={category._id}
              onChange={async (e) => {
                const confirm = window.confirm(
                  'آیا مطمئن هستید که می خواهید این دسته بندی را اضافه کنید؟'
                );
                if (confirm) {
                  setSelectedFilterChild([]);
                  const categoryId = category._id;
                  await axios
                    .get(`${api}/api/filter/category/${categoryId}`, {
                      headers,
                    })
                    .then((res) => {
                      setFilters(res.data);
                    })
                    .catch((err) => {
                      console.log(err);
                    });

                  setProduct({
                    ...product,
                    categories: e.target.value,
                  });
                }
              }}
              checked={product?.categories === category._id}
            />
            {category?.name}
            {category?.children?.length > 0 && (
              <ul style={{ padding: 0, paddingRight: 10 }}>
                {renderCategories(category.children)}
              </ul>
            )}
          </li>
        </ul>
      );
    }

    return myCategories;
  };

  useEffect(async () => {
    const hRef = window.location?.href?.indexOf('=');
    const productId = window.location?.href?.slice(hRef + 1);

    if (hRef !== -1) {
      setUpdateMode(true);
      const response = await axios.get(`${api}/api/product/${productId}`);
      setProduct({
        name: response?.data?.name,
        description: response?.data?.description,
        price: response?.data?.price,
        categories: response?.data?.categories,
        images: response?.data?.images,
        filters: response?.data?.filters,
      });
      setFilter(response?.data?.filters);
      await axios
        .get(`${api}/api/filter/category/${response?.data?.categories}`, {
          headers,
        })
        .then((res) => {
          setFilters(res.data);
        });
      setFile(
        `${api}/uploads/admin/product/${productId}/${
          response?.data?.images?.[response?.data?.images?.length - 1]
        }`
      );
      setImages(
        response?.data?.images?.map((el) => {
          return `${api}/uploads/admin/product/${productId}/${el}`;
        })
      );
    }
    const response = await axios.get(`${api}/api/categories`);
    setCategoeies(response?.data?.categoryList);
  }, []);

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    rtl: true,
  };

  function removeDuplicates(originalArray, prop) {
    var newArray = [];
    var lookupObject = {};

    for (var i in originalArray) {
      lookupObject[originalArray[i][prop]] = originalArray[i];
    }

    for (i in lookupObject) {
      newArray.push(lookupObject[i]);
    }
    return newArray;
  }

  const commafy = (num) => {
    var array = num.toString().split('');
    var index = -3;
    while (array.length + index > 0) {
      array.splice(index, 0, ',');
      // Decrement by 4 since we just added another unit to the array.
      index -= 4;
    }
    return array.join('');
  };

  return (
    <CContainer>
      <CRow>
        <CCol md={12}>
          <div class='d-flex flex-row docs-highlight mb-3 justify-content-center'>
            <div class='p-2 docs-highlight'>
              <CForm onSubmit={handleSubmit} style={{ minWidth: 800 }}>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>نام محصول</p>
                  </CFormLabel>
                  <CFormInput
                    type='text'
                    id='name'
                    placeholder='نام محصول را اینجا وارد کنید'
                    name='name'
                    onChange={handleChange}
                    value={product.name}
                  />
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>دسته بندی محصول</p>
                  </CFormLabel>
                  <div className='d-flex justify-content-between flex-wrap'>
                    {categories && renderCategories(categories)}
                  </div>
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>افزودن فیلتر</p>
                  </CFormLabel>
                  <div className='d-flex  flex-wrap'>
                    {filters?.map((el, idx) => (
                      <div
                        className='d-flex flex-column p-4 pt-0'
                        style={{ cursor: 'pointer' }}
                      >
                        <div className='d-flex'>
                          <div className='d-flex'>
                            <div>
                              <p className='fs-5'>{el.name}</p>
                              <select
                                // value={filter[idx].value}
                                style={{ minWidth: 150, margin: '0 8px 8px' }}
                                onChange={async (e) => {
                                  const result = await axios.get(
                                    `${api}/api/filter/category/value/${el?._id}/${e?.target?.value}`,
                                    { headers }
                                  );
                                  if (result?.data?.[0]?.values?.length) {
                                    setParent(el?._id);
                                    let arr = [...subFilters];
                                    let filter = arr.filter(
                                      (ell) => ell.parent !== el?._id
                                    );
                                    filter.push(
                                      result?.data?.[0]?.values?.map((eee) => {
                                        return {
                                          ...eee,
                                          parent: el?._id,
                                        };
                                      })
                                    );

                                    setSubFilters(filter?.flat());
                                  }
                                  setFilter(
                                    removeDuplicates(
                                      [
                                        ...filter,
                                        {
                                          parent: el?._id,
                                          value: e?.target?.value,
                                          name: el?.name,
                                        },
                                      ],
                                      'parent'
                                    )
                                  );
                                }}
                              >
                                <option>انتخاب کنید</option>
                                {el?.values?.map((value) => (
                                  <option value={value?._id} name={value?.name}>
                                    {value?.name}
                                  </option>
                                ))}
                              </select>
                            </div>
                            {subFilters?.length && el.children.title ? (
                              <div>
                                <p className='fs-5'>
                                  {el.children.title
                                    ? el.children.title
                                    : 'بدون عنوان'}
                                </p>
                                <select
                                  style={{ minWidth: 150, marginBottom: 8 }}
                                  onChange={(e) => {
                                    let arr = [...subFilter];
                                    let filter = arr.filter(
                                      (ell) => ell.parent !== el?._id
                                    );
                                    filter.push({
                                      parent: el?._id,
                                      value: e?.target?.value,
                                      title: el?.children?.title,
                                    });
                                    setSubFilter(filter?.flat());
                                  }}
                                >
                                  <option>انتخاب کنید</option>
                                  {subFilters?.map((sub) => {
                                    if (sub?.parent === el?._id) {
                                      return (
                                        <option value={sub?._id}>
                                          {sub?.name}
                                        </option>
                                      );
                                    }
                                  })}
                                </select>
                              </div>
                            ) : null}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>عکس محصول</p>
                  </CFormLabel>
                  <div
                    style={{ width: 500, marginBottom: 48, display: 'flex' }}
                  >
                    {images?.length &&
                      images.map((el) => (
                        <div
                          style={{
                            position: 'relative',
                            width: 300,
                            border: '1px solid #ccc',
                            margin: '0 8px 8px',
                          }}
                        >
                          <div>
                            <CIcon
                              icon={cilTrash}
                              style={{
                                color: 'red',
                                cursor: 'pointer',
                                position: 'absolute',
                                top: 10,
                                left: 10,
                                borderRadius: '50%',
                                border: '1px solid red',
                                padding: 3,
                                width: 25,
                                height: 25,
                                backgroundColor: '#fff',
                              }}
                              title='حذف عکس'
                              onClick={() => {
                                const filter = images.filter(
                                  (elem) => elem !== el
                                );

                                setImages(filter);
                                setProduct({
                                  ...product,
                                  images: product?.images?.filter(
                                    (image) => image !== el.split('/').pop()
                                  ),
                                });
                              }}
                            />
                          </div>
                          <img
                            width={'100%'}
                            src={el}
                            alt='slider'
                            style={{
                              width: '100%',
                              height: '100%',
                              objectFit: 'cover',
                            }}
                          />
                        </div>
                      ))}
                  </div>
                  <div className='form-file'>
                    <input
                      multiple
                      type='file'
                      className='form-file-input'
                      id='customFile'
                      style={{ display: 'none' }}
                      onChange={(e) => {
                        console.log(e.target.files);
                        setFileData([...fileData, e.target.files[0]]);
                        setFile(URL.createObjectURL(e.target.files[0]));
                        setImages([
                          ...images,
                          URL.createObjectURL(e.target.files[0]),
                        ]);
                      }}
                    />
                    <label className='form-file-label' htmlFor='customFile'>
                      <span className=' m-3'>عکس محصول را انتخاب کنید</span>
                      <span className='btn btn-primary'>افزودن عکس</span>
                    </label>
                  </div>
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>قیمت (تومان)</p>
                  </CFormLabel>
                  <CFormInput
                    type='text'
                    id='price'
                    placeholder='قیمت محصول'
                    name='price'
                    onChange={handleChange}
                    value={addCommas(product.price)}
                  />
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>درصد تخفیف</p>
                  </CFormLabel>
                  <CFormInput
                    type='number'
                    id='discount'
                    placeholder='بین ۰ تا ۱۰۰'
                    name='discount'
                    onChange={handleChange}
                    value={product.discount}
                  />
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>توضیحات محصول</p>
                  </CFormLabel>
                  <CFormTextarea
                    id='productDescription'
                    rows='3'
                    name='description'
                    onChange={handleChange}
                    value={product.description}
                    maxlength='300'
                  ></CFormTextarea>
                </div>

                <div className='mb-3'>
                  <CButton color='success' type='submit'>
                    {updateMode ? 'ویرایش' : 'افزودن'}
                  </CButton>
                </div>
              </CForm>
            </div>
          </div>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default AddProduct;
