import { cilPen, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CButton,
  CFormSwitch,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { api, headers } from 'src/views/api';
import Filter from './Filter';

const Products = () => {
  const [products, setProducts] = useState([]);
  const role = localStorage.role;
  const userId = localStorage.userId;
  const { push } = useHistory();

  const handleDeleteProduct = async (id) => {
    const confirm = window.confirm('از حذف این محصول اطمینان دارید؟');

    if (confirm) {
      try {
        const response = await axios.delete(`${api}/api/product/${id}`, {
          headers,
        });

        if (response?.status === 200) {
          if (role === 'admin') {
            const response = await axios.get(`${api}/api/products/${userId}`);
            setProducts(response?.data);
          } else {
            const response = await axios.get(`${api}/api/products`);
            setProducts(response?.data);
          }
        }
      } catch (error) {
        alert(error?.response?.data?.message);
      }
    }
  };

  useEffect(async () => {
    if (role === 'admin') {
      const response = await axios.get(`${api}/api/products/${userId}`);
      setProducts(response?.data);
    } else {
      const response = await axios.get(`${api}/api/products`);
      setProducts(response?.data);
    }
  }, []);

  const filterFunction = async (e) => {
    const id = e.target.value;

    if (id) {
      const response = await axios.get(`${api}/api/products/category/${id}`);
      setProducts(response?.data);
    } else {
      const response = await axios.get(`${api}/api/products`);
      setProducts(response?.data);
    }
  };

  return (
    <div>
      <div>
        <Filter filterFunction={filterFunction} />
      </div>
      <div>
        {products?.length ? (
          <CTable hover responsive>
            <CTableHead>
              <CTableRow>
                <CTableHeaderCell scope='col'>#</CTableHeaderCell>
                <CTableHeaderCell scope='col'>تصویر</CTableHeaderCell>
                <CTableHeaderCell scope='col'>نام محصول</CTableHeaderCell>
                <CTableHeaderCell scope='col'>توضیحات</CTableHeaderCell>
                <CTableHeaderCell scope='col'>قیمت</CTableHeaderCell>
                <CTableHeaderCell scope='col'>دسته بندی</CTableHeaderCell>
                {localStorage?.role === 'super_admin' && (
                  <CTableHeaderCell scope='col'>مالک</CTableHeaderCell>
                )}
                <CTableHeaderCell scope='col'>عملیات</CTableHeaderCell>
                {localStorage?.role === 'super_admin' && (
                  <CTableHeaderCell scope='col'>انتشار</CTableHeaderCell>
                )}
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {products?.map((el, i) => (
                <CTableRow key={el?._id}>
                  <CTableHeaderCell scope='row'>{i + 1}</CTableHeaderCell>
                  <CTableDataCell>
                    {el?.images?.length ? (
                      <img
                        width={30}
                        src={`${api}/uploads/admin/product/${el?._id}/${
                          el?.images?.[el?.images?.length - 1]
                        }`}
                      />
                    ) : (
                      <div
                        style={{ width: 30, height: 20, background: '#f9f9f9' }}
                      />
                    )}
                  </CTableDataCell>
                  <CTableDataCell>{el?.name}</CTableDataCell>
                  <CTableDataCell title={el?.description}>
                    {el?.description?.length > 14
                      ? el?.description?.slice(0, 14) + '...'
                      : el?.description}
                  </CTableDataCell>
                  <CTableDataCell>{el?.price}</CTableDataCell>
                  <CTableDataCell>
                    {el?.categories?.name
                      ? el?.categories?.name
                      : 'بدون دسته بندی'}
                  </CTableDataCell>
                  {localStorage?.role === 'super_admin' && (
                    <CTableDataCell>{`${el?.user?.firstName} ${el?.user?.lastName}`}</CTableDataCell>
                  )}
                  <CTableDataCell>
                    <CButton
                      color='danger'
                      onClick={() => handleDeleteProduct(el?._id)}
                    >
                      <CIcon icon={cilTrash} />
                    </CButton>
                    <CButton
                      className='mx-2'
                      color='info'
                      onClick={() => push(`/products/edit/${el?._id}`)}
                    >
                      <CIcon icon={cilPen} />
                    </CButton>
                  </CTableDataCell>
                  {localStorage?.role === 'super_admin' && (
                    <CTableDataCell>
                      <CFormSwitch
                        onChange={async () => {
                          const confirm = window.confirm(
                            'از تغییر این محصول اطمینان دارید؟'
                          );

                          if (confirm) {
                            await axios.put(`${api}/api/product/${el?._id}`, {
                              ...el,
                              published: !el?.published,
                            });
                            const response = await axios.get(
                              `${api}/api/products`
                            );
                            setProducts(response?.data);
                          }
                        }}
                        checked={el.published}
                        color='success'
                      />
                    </CTableDataCell>
                  )}
                </CTableRow>
              ))}
            </CTableBody>
          </CTable>
        ) : (
          'محصولی یافت نشد'
        )}
      </div>
    </div>
  );
};

export default Products;
