import React from "react";
import Plan from "./views/pages/plan/Plan";
import AddProduct from "./views/pages/products/addProduct/AddProduct";
import EditProduct from "./views/pages/products/addProduct/EditProduct";
import AddCategory from "./views/pages/products/category/AddCategory";
import Products from "./views/pages/products/products/Products";
import Profile from "./views/pages/profile/Profile";

const Dashboard = React.lazy(() => import("./views/pages/dashboard/Dashboard"));
const Filter = React.lazy(() => import("./views/pages/products/filter/Filter"));

const routes = [
  { path: "/", exact: true, name: "خانه" },
  { path: "/dashboard", name: "داشبورد", component: Dashboard },
  { path: "/products", name: "محصولات", component: Products, exact: true },
  {
    path: "/products/add",
    name: "افزودن محصول",
    component: AddProduct,
    exact: true,
  },
  {
    path: "/products/edit/:id",
    name: "ویرایش محصول",
    component: EditProduct,
    exact: true,
  },
  {
    path: "/products/add-category",
    name: "افزودن دسته بندی",
    component: AddCategory,
    exact: true,
  },
  {
    path: "/categories",
    name: "فیلتر",
    component: Filter,
    exact: true,
  },
  {
    path: "/profile",
    name: "پروفایل",
    component: Profile,
    exact: true,
  },
  {
    path: "/plan",
    name: "پلن ها",
    component: Plan,
    exact: true,
  },
];

export default routes;
