import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
} from '@coreui/react';
import React, { useState, useEffect } from 'react';
import CategoriesList from './CategoriesList';
import { api } from 'src/views/api';
import axios from 'axios';
import { toast } from 'react-toastify';

const AddCategory = () => {
  const [categories, setCategories] = useState([]);
  const [rawCategories, setRawCategories] = useState([]);
  const [selectedCategories, setSelectedCategories] = useState([]);

  const [checkedState, setChecked] = useState([]);
  const [expandedState, setExpanded] = useState([]);
  const [updateCtaegoryModal, setUpdateCtaegoryModal] = useState(false);
  const [addCategoryModal, setAddCategoryModal] = useState(false);
  const [addCategory, setAddCategory] = useState(null);

  useEffect(() => {
    const categoryList = [];
    for (const cat in checkedState) {
      const find = rawCategories.find(
        (category) => category._id === checkedState[cat]
      );
      categoryList.push(find);
    }

    setSelectedCategories(categoryList);
  }, [checkedState, expandedState]);

  const renderCategories = (categories) => {
    let myCategories = [];
    for (let category of categories) {
      myCategories.push({
        label: category.name,
        value: category._id,
        parent: category.parent,
        children:
          category?.children?.length > 0 && renderCategories(category.children),
      });
    }

    return myCategories;
  };

  useEffect(() => {
    (async () => {
      const categories = await axios.get(`${api}/api/categories`);

      setCategories(renderCategories(categories?.data?.categoryList));
      setRawCategories(categories?.data?.categories);
    })();
  }, []);

  const handleUpdateCategory = (value, id) => {
    const arr = [...selectedCategories];
    const target = arr?.find((e) => e._id === id);
    target.name = value;

    setSelectedCategories(arr);
  };

  return (
    <CContainer>
      <CRow>
        <CCol md='8'>
          <CategoriesList
            {...{
              categories,
              setChecked,
              setExpanded,
              checkedState,
              expandedState,
            }}
          />
        </CCol>
        <CCol md='2'>
          {/* add category */}
          <CButton
            color='success'
            onClick={() => setAddCategoryModal(!addCategoryModal)}
          >
            افزودن دسته بندی
          </CButton>
          <CModal
            visible={addCategoryModal}
            onClose={() => setAddCategoryModal(false)}
          >
            <CModalHeader onClose={() => setAddCategoryModal(false)}>
              <CModalTitle>افزودن دسته بندی</CModalTitle>
            </CModalHeader>
            <CModalBody>
              <CRow>
                <CCol md='12'>
                  <CForm
                    onSubmit={async (e) => {
                      e.preventDefault();
                      const res = await axios.post(
                        `${api}/api/category`,
                        addCategory
                      );

                      if (res.status == 200) {
                        const categories = await axios.get(
                          `${api}/api/categories`
                        );

                        setCategories(
                          renderCategories(categories?.data?.categoryList)
                        );
                        setRawCategories(categories?.data?.categories);
                        toast.success('با موفقیت اضافه شد');
                      }
                    }}
                  >
                    <div className='mb-3'>
                      <CFormLabel>نام دسته بندی</CFormLabel>
                      <CFormInput
                        type='text'
                        value={addCategory?.name}
                        className='mb-3'
                        onChange={(e) =>
                          setAddCategory({
                            ...addCategory,
                            name: e.target.value,
                          })
                        }
                      />
                      <CFormSelect
                        className='mb-3'
                        value={addCategory?.parent}
                        onChange={(e) =>
                          setAddCategory({
                            ...addCategory,
                            parent: e.target.value,
                          })
                        }
                        options={[
                          'انتخاب دسته بندی اصلی',
                          ...rawCategories?.map((el) => {
                            return {
                              value: el._id,
                              label: el.name,
                            };
                          }),
                        ]}
                      />
                      <CButton type='submit' color='primary'>
                        افزودن
                      </CButton>
                    </div>
                  </CForm>
                </CCol>
              </CRow>
            </CModalBody>
          </CModal>
        </CCol>
        <CCol md='2'>
          <CButton
            onClick={() => setUpdateCtaegoryModal(!updateCtaegoryModal)}
            disabled={selectedCategories?.length === 0}
          >
            بروزرسانی / حذف
          </CButton>
          <CModal
            visible={updateCtaegoryModal}
            onClose={() => setUpdateCtaegoryModal(false)}
          >
            <CModalHeader onClose={() => setUpdateCtaegoryModal(false)}>
              <CModalTitle>بروزرسانی / حذف</CModalTitle>
            </CModalHeader>
            <CModalBody>
              {selectedCategories?.map((category) => (
                <CRow>
                  <CCol md='12'>
                    <CForm
                      onSubmit={async (e) => {
                        e.preventDefault();
                        const res = await axios.put(
                          `${api}/api/category/${category?._id}`,
                          {
                            ...category,
                            name: category?.name,
                          }
                        );

                        if (res.status == 200) {
                          const categories = await axios.get(
                            `${api}/api/categories`
                          );

                          setCategories(
                            renderCategories(categories?.data?.categoryList)
                          );
                          setRawCategories(categories?.data?.categories);

                          setSelectedCategories([]);

                          toast.success('با موفقیت بروزرسانی شد');
                        }
                      }}
                    >
                      <div className='mb-3'>
                        <CFormLabel htmlFor='exampleInputEmail1'>
                          نام دسته بندی
                        </CFormLabel>
                        <CFormInput
                          type='text'
                          aria-describedby='emailHelp'
                          value={category?.name}
                          className='mb-3'
                          onChange={(e) =>
                            handleUpdateCategory(e.target.value, category._id)
                          }
                        />
                        <CButton type='submit' color='primary'>
                          بروزرسانی
                        </CButton>{' '}
                        <CButton
                          onClick={async (e) => {
                            e.preventDefault();
                            const res = await axios.delete(
                              `${api}/api/category/${category._id}`
                            );

                            if (res.status == 200) {
                              const categories = await axios.get(
                                `${api}/api/categories`
                              );

                              setCategories(
                                renderCategories(categories?.data?.categoryList)
                              );
                              setRawCategories(categories?.data?.categories);
                              toast.error('با موفقیت حذف شد');
                              setSelectedCategories(
                                selectedCategories?.filter(
                                  (el) => el._id !== category._id
                                )
                              );
                            }
                          }}
                          color='danger'
                        >
                          حذف
                        </CButton>
                      </div>
                    </CForm>
                  </CCol>
                </CRow>
              ))}
            </CModalBody>
          </CModal>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default AddCategory;
