import {
  cilCaretBottom,
  cilCaretLeft,
  cilCheckCircle,
  cilMinus,
} from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import React from 'react';
import CheckboxTree from 'react-checkbox-tree';
import 'react-checkbox-tree/lib/react-checkbox-tree.css';

const CategoriesList = ({
  categories,
  setChecked,
  setExpanded,
  checkedState,
  expandedState,
}) => {
  const onCheck = (checked, d) => {
    let checkedItems = checked;
    checkedItems.push(d.value);

    setChecked(checkedItems);
  };

  const onExpand = (expanded) => {
    setExpanded(expanded);
  };

  return (
    <div>
      <CheckboxTree
        nodes={categories}
        checked={checkedState}
        expanded={expandedState}
        onCheck={onCheck}
        onExpand={onExpand}
        icons={{
          check: <CIcon icon={cilCheckCircle} />,
          uncheck: <CIcon icon={cilMinus} />,
          halfCheck: <CIcon icon={cilMinus} />,
          expandClose: <CIcon icon={cilCaretLeft} />,
          expandOpen: <CIcon icon={cilCaretBottom} />,
          parentClose: <CIcon icon={cilMinus} />,
          parentOpen: <CIcon icon={cilMinus} />,
          leaf: <CIcon icon={cilMinus} />,
        }}
      />
    </div>
  );
};

export default CategoriesList;
