import { cilPen, cilTrash } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import {
  CButton,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from "@coreui/react";
import axios from "axios";
import { useEffect, useState } from "react";
import { api, headers } from "../../api";
import CreateModal from "./CreateModal";

const Plan = () => {
  const [plans, setPlans] = useState([]);

  const getAll = async () => {
    const plans = await axios.get(`${api}/api/plans`, {
      headers: headers,
    });

    setPlans(plans.data);
  };

  useEffect(() => {
    getAll();
  }, []);

  const handleDelete = async (id) => {
    const confirm = window.confirm("از حذف این پلن اطمینان دارید؟");

    if (confirm) {
      try {
        const response = await axios.delete(`${api}/api/plan/${id}`, {
          headers,
        });

        if (response?.status === 200) {
          getAll();
        }
      } catch (error) {
        alert(error?.response?.data?.message);
      }
    }
  };

  return (
    <div>
      <div style={{ marginBottom: 20 }}>
        <CreateModal getAll={getAll} />
      </div>
      {plans?.length ? (
        <CTable hover responsive>
          <CTableHead>
            <CTableRow>
              <CTableHeaderCell scope="col">#</CTableHeaderCell>
              <CTableHeaderCell scope="col">نام پلن</CTableHeaderCell>
              <CTableHeaderCell scope="col">قیمت</CTableHeaderCell>
              <CTableHeaderCell scope="col">توضیحات</CTableHeaderCell>
              <CTableHeaderCell scope="col">عملیات</CTableHeaderCell>
            </CTableRow>
          </CTableHead>
          <CTableBody>
            {plans?.map((el, i) => (
              <CTableRow key={el?._id}>
                <CTableHeaderCell scope="row">{i + 1}</CTableHeaderCell>
                <CTableDataCell>{el?.name}</CTableDataCell>
                <CTableDataCell>{el?.price}</CTableDataCell>
                <CTableDataCell title={el?.description}>
                  {el?.description?.length > 14
                    ? el?.description?.slice(0, 14) + "..."
                    : el?.description}
                </CTableDataCell>
                <CTableDataCell>
                  <CButton color="danger" onClick={() => handleDelete(el?._id)}>
                    <CIcon icon={cilTrash} />
                  </CButton>
                  <CButton className="mx-2" color="info">
                    <CIcon icon={cilPen} />
                  </CButton>
                </CTableDataCell>
              </CTableRow>
            ))}
          </CTableBody>
        </CTable>
      ) : (
        "پلنی یافت نشد"
      )}
    </div>
  );
};

export default Plan;
